body.page-my-training
{
	.my-training-list
	{
		.training
		{
			margin-bottom:20px;
			border-radius:5px;
			box-shadow:1px 1px 2px 1px #999;
			padding:10px;
			
			@media (max-width:@screen-xs-max)
			{
				padding:5px;
			}

			.name
			{
				h2
				{
					margin-top:0;
				}
			}

			.dates
			{
				display:flex;
				font-size:20px;
				border-top:1px solid #aaa;
				border-bottom:1px solid #aaa;
				
				.date-start, .date-end
				{
					width:50%;

					.title
					{
						width:100%;
						text-align:center;
						font-weight:bold;
					}
					.content
					{
						width:100%;
						text-align:center;
					}
				}
			}
			.others
			{
				display:flex;
				flex-wrap:wrap;
				border-bottom:1px solid #aaa;
				padding-top:10px;
				
				.objectives, .environments
				{
					width:50%;

					@media (max-width:@screen-xs-max)
					{
						width:100%;
					}

					.title
					{
						width:100%;
						text-align:center;
						font-weight:bold;
					}
					.content
					{
						width:100%;
						text-align:center;
					}
				}
			}
			.description
			{
				padding-top:10px;
			}

			
		}
	}

	.exercises
	{
		//background-image:linear-gradient(to right,@brand-primary 0, lighten(@brand-primary,3%) 100%);

		.exercise-category
		{
			h3
			{
				background-image:linear-gradient(45deg,@brand-primary 0, lighten(@brand-primary,3%) 100%);
				box-shadow:1px 1px 1px 0 #999;
				padding:5px;
			}

			.category-exercises
			{
				.exercise
				{
					width:100%;
					margin-bottom:20px;

					padding:5px;
					box-shadow:1px 1px 2px 1px #999;
					border-radius:5px;

					.basic
					{
						display:flex;
						margin-bottom:10px;
						padding:10px;
						border-radius:5px;


						@media (max-width:@screen-sm-max)
						{
							flex-wrap:wrap;
							padding:5px;
						}

						.name
						{
							width:45%;

							@media (max-width:@screen-sm-max)
							{
								width:100%;
							}
						}

						.data
						{
							width:45%;

							@media (max-width:@screen-sm-max)
							{
								width:100%;
							}

							.current
							{
								display:flex;
								justify-content:space-between;

								@media (max-width:@screen-sm-max)
								{
									flex-wrap:wrap;
								}

								.series, .repetitions, .loads
								{
									width:33%;

									@media (max-width:@screen-sm-max)
									{
										width:100%;
									}
								}

								.loads
								{
									.form-group
									{
										.input-group-addon
										{
											cursor:pointer;
										}

										.glyphicon.error
										{
											display:none;
										}
										.glyphicon.success
										{
											display:none;
										}

										&.has-success
										{
											.glyphicon.send
											{
												display:none;
											}
											.glyphicon.success
											{
												display:inline-block;
											}
										}
										&.has-error
										{
											.glyphicon.send
											{
												display:none;
											}
											.glyphicon.error
											{
												display:inline-block;
											}
										}
									}
								}
							}
							.historial
							{
								.historial-row
								{
									display:flex;
									justify-content:space-between;

									> div
									{
										display:flex;
										align-items:center;
									}

									&.head
									{
										//display:none;
										font-weight:700;
										@media (max-width:@screen-sm-max)
										{
											display:flex;
										}
									}

									.remove
									{
										min-width:36px;

										button
										{
											margin-bottom:5px;
											margin-top:5px;
										}
									}

									.date
									{
										min-width:80px;
									}

									.series, .repetitions, .loads
									{
										width:33%;
										overflow:hidden;
										padding:0 4px;
									}
								}
							}
						}

						.more
						{
							width:10%;
							display:flex;
							flex-direction:column;
							align-items:center;
							justify-content:center;

							@media (max-width:@screen-sm-max)
							{
								width:100%;
								padding-top:10px;
								padding-bottom:10px;
								flex-direction:row;
								justify-content:space-evenly;
							}

							button:first-child
							{
								margin-bottom:10px;
								@media (max-width:@screen-sm-max)
								{
									margin-bottom:0;
								}
							}
						}
					}

					.details
					{
						padding:10px;
						border-radius:5px;
						box-shadow:inset 1px 1px 2px 1px #999;

						@media (max-width:@screen-xs-max)
						{
							padding:5px;
						}
						
						.images
						{
							display:flex;
							justify-content:space-around;
							align-items:center;

							margin-bottom:20px;

							img
							{
								width:40%;
								max-width:300px;
								height:auto;
							}
						}

						.description
						{
							margin-bottom:20px;

							text-align:center;
							font-size:16px;

							@media (max-width:@screen-xs-max)
							{
								font-size:14px;
							}
						}

						
						.video-layer
						{
							position:relative;

							.video
							{
								position:relative;
								width:100%;
								padding-bottom:56.25%;

								&.empty
								{
									padding-bottom:0;
								}
								
								iframe
								{
									position:absolute;
									top:0;
									left:0;
									width:100%;
									height:100%;
								}
							}
						}
					}
				}
			}
		}

	}
}
