body.page-view-member-diets
{
	.diet-row
	{
		display:flex;
		flex-wrap:wrap;

		margin-bottom:40px;

		&.diet-inactive
		{
			opacity:.5;
		}

		.diet-col
		{
			&.diet-col-2
			{
				@media (max-width:@screen-xs-max )
				{
					margin-bottom:20px;
				}
			}

			.diet-col-content
			{
				background-color:#ddd;
				text-align:center;
				font-size:20px;
				color:#444;

				padding:25px;
				height:100%;

				&.diet-col-content-2
				{
					@media (max-width:@screen-xs-max )
					{
						padding-top:0;
					}
				}

				.date
				{
					color:@brand-primary;
				}

				.diet-name
				{
					font-size:18px;
					text-transform:uppercase;
				}
			}

			&.diet-info-col
			{
				.diet-info-col-content
				{
					position:relative;

					background-color:@brand-primary;
					color:#fff;
					padding:25px;
					@media (max-width:@screen-xs-max)
					{
						padding-top:36px;
					}
					margin-top:25px;
					margin-bottom:25px;

					.actions
					{
						position:absolute;
						top:0;
						right:0;

						padding-right:5px;
						padding-top:5px;

						.diet-button
						{
							background-color:#fff;
							color:darken(@brand-primary,10%);

							font-size:12px;
							line-height:1;
							padding:4px 12px;
							border-radius:8px;
							border:0;
							margin-left:5px;

							&:hover
							{
								color:@brand-primary;
							}
						}
					}

					h3
					{
						margin-top:0;
					}

					.video-layer
					{
						position:relative;
						max-width:560px;
						margin-left:auto;
						margin-right:auto;

						.video
						{
							position:relative;
							width:100%;
							padding-bottom:56.25%;
							
							iframe
							{
								position:absolute;
								top:0;
								left:0;
								width:100%;
								height:100%;
							}
						}
					}

					.exercises
					{
						.exercise-category
						{
							.category-exercises
							{
								.exercise
								{
									.basic
									{
										display:flex;
										width:100%;

										.name
										{
											flex-grow:2;
										}

										.data
										{
											.current
											{
												display:flex;

												> div
												{
													padding:0 10px;
												}

												.loads
												{
													.error, .success
													{
														display:none;
													}
												}
											}
										}
									}

			
									.details
									{
										width:100%;

										.images
										{
											display:flex;
											justify-content:space-around;
											align-items:center;

											margin-bottom:20px;

											img
											{
												max-width:40%;
											}
										}

										.description
										{
											margin-bottom:20px;
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}

	.assign-diet-row
	{
		background-color:#ddd;
		padding-top:20px;

		.col-new-diet
		{
			display:none;

			.categories
			{
				
				.exercise-category
				{
					display:flex;
					align-items:flex-start;

					border:1px solid #aaa;
					border-radius:2px;
					padding:2px;
					
					@media (max-width:@screen-xs-max)
					{
						flex-wrap:wrap;
					}

					&.hovering
					{
						padding-bottom:20px;
						background-color:yellow;
					}

					.name
					{
						width:20%;
						display:flex;

						@media (max-width:@screen-xs-max)
						{
							width:100%;
						}

						.dragger
						{
							padding:7px 10px 0 10px;
							cursor:hand;
						}

						.text
						{
							padding:7px 4px 0 4px;
						}
					}

					.exercises
					{
						width:80%;
						
						@media (max-width:@screen-xs-max)
						{
							width:100%;
						}

						.exercise
						{
							display:flex;
							@media (max-width:@screen-xs-max)
							{
								flex-wrap:wrap;
							}

							.remove-layer
							{
								
							}	

							.form-group
							{
								margin-bottom:0;
							}
					
							.select-form-group
							{
								width:100%;
							}

							.input-form-group
							{
								min-width:80px;

								@media (max-width:@screen-xs-max)
								{
									width:33%;
								}
							}
						}
					}

					.exercise-template
					{
						display:none;
					}
				}
			}

			textarea
			{
				width:100%;
				margin-bottom:20px;
			}
		}

		.col-existing-diet
		{
			display:none;

			.select-environments-layer
			{
			}
			.select-environments-layer
			{
			}

			textarea
			{
				width:100%;
				margin-bottom:20px;
			}
		}
	}
}

