.page-header-video-container
{
	h1
	{
		margin-top:40px;
		margin-bottom:40px;
		position:relative;
	}

	h2
	{
		margin-top:30px;
		margin-bottom:30px;
		position:relative;
	}

	.video-layer
	{
		position:relative;

		.video
		{
			position:relative;
			width:100%;
			padding-bottom:56.25%;

			&.empty
			{
				padding-bottom:0;
			}
			
			iframe
			{
				position:absolute;
				top:0;
				left:0;
				width:100%;
				height:100%;
			}
		}
	}
}
