.tools-bar
{
	background-color:@brand-primary;
	background-image:linear-gradient(45deg,darken(@brand-primary,5%) 0, lighten(@brand-primary,5%) 100%); 
	box-shadow:1px 1px 1px 0 #666;
	
	padding:10px;
	margin-bottom:@line-height-computed;

	display:flex;
	justify-content:space-between;
	align-items:center;
	flex-wrap:wrap;

	&.right
	{
		justify-content:flex-end;
	}
}

@break-items-table-max:@screen-xs-max;

table.items-table
{
	a
	{
		color:darken(@brand-primary,20%);
		&:hover { color:@brand-primary; }
	}

	> thead
	{
		@media (max-width: @break-items-table-max)
		{
			display:none;
		}
	}

	> tbody > tr
	{
		@media (max-width: @break-items-table-max)
		{
			display:flex;
			flex-wrap:wrap;
			justify-content:center;
		}

		&.hide-row
		{
			display:none;
		}
	}

	> tbody > tr > td
	{
		vertical-align:middle;

		@media (max-width: @break-items-table-max)
		{
			border-top:0;
		}

		&.small-full-width
		{
			@media (max-width: @break-items-table-max)
			{
				width:100%;
			}
		}
		&.small-medium-width
		{
			@media (max-width: @break-items-table-max)
			{
				width:50%;
			}
		}

		&.actions
		{
			font-size:20px;
			text-align:right;

			&.two
			{
				min-width:120px;
			}
			&.three
			{
				min-width:170px;
			}
			
			a
			{
				display:inline-block;
				margin-left: 5px;

				background-color:@brand-primary;
				color:#333;
				border:2px solid #fff;
				border-radius:100%;
				box-shadow:1px 1px 2px 0 #666;

				width: 40px;
				text-align: center;
				height: 40px;
				padding-top: 5px;
				background-image: radial-gradient(@brand-primary 0, darken(@brand-primary,10%) 100%);

				&:hover
				{
					color:#fff;
					box-shadow:0 0 2px 1px @brand-primary;
				}
			}

			@media (max-width: @break-items-table-max)
			{
				width:100%;
				display:flex;
				justify-content:flex-end;
				
				a
				{
				}
			}
		}

		.cell-label
		{
			display:none;
			font-size:10px;

			@media (max-width: @break-items-table-max)
			{
				display:block;
			}
		}
	}

}
