body.page-view-member-training
{
	.training-row
	{
		display:flex;
		flex-wrap:wrap;

		margin-bottom:40px;

		&.training-inactive
		{
			opacity:.5;
		}

		.training-col
		{
			&.training-col-2
			{
				@media (max-width:@screen-xs-max )
				{
					margin-bottom:20px;
				}
			}

			.training-col-content
			{
				background-color:#ddd;
				text-align:center;
				font-size:20px;
				color:#444;

				padding:25px;
				height:100%;

				&.training-col-content-2
				{
					@media (max-width:@screen-xs-max )
					{
						padding-top:0;
					}
				}

				.date
				{
					color:@brand-primary;
				}

				.training-name
				{
					font-size:18px;
					text-transform:uppercase;
				}

			}

			&.training-info-col
			{
				.training-info-col-content
				{
					position:relative;

					background-color:@brand-primary;
					color:#fff;
					padding:25px;
					@media (max-width:@screen-xs-max)
					{
						padding-top:36px;
					}
					margin-top:25px;
					margin-bottom:25px;

					.actions
					{
						position:absolute;
						top:0;
						right:0;

						padding-right:5px;
						padding-top:5px;

						.training-button
						{
							background-color:#fff;
							color:darken(@brand-primary,10%);

							font-size:12px;
							line-height:1;
							padding:4px 12px;
							border-radius:8px;
							border:0;
							margin-left:5px;

							&:hover
							{
								color:@brand-primary;
							}
						}
					}

					h3
					{
						margin-top:0;
					}

					.video-layer
					{
						position:relative;
						max-width:560px;
						margin-left:auto;
						margin-right:auto;

						.video
						{
							position:relative;
							width:100%;
							padding-bottom:56.25%;
							
							iframe
							{
								position:absolute;
								top:0;
								left:0;
								width:100%;
								height:100%;
							}
						}

					}

					.exercises
					{
						.exercise-category
						{
							.category-exercises
							{
								.exercise
								{
									.basic
									{
										display:flex;
										width:100%;
										flex-wrap:nowrap;

										//@media (orientation:portrait) and (max-width:700px)
										@media (max-width:@screen-sm-max)
										{
											flex-wrap:wrap;
										}

										.name
										{
											flex-grow:2;

											@media (max-width:@screen-sm-max)
											{
												width:100%;
											}
										}

										.data
										{

											@media (max-width:@screen-sm-max)
											{
												width:100%;
											}

											.current
											{
												display:flex;
												flex-wrap:wrap;
												justify-content:center;

												> div
												{
													padding:0 10px;
													text-align:center;
												}

												.loads
												{
													.error, .success
													{
														display:none;
													}

													.success-msg
													{
														display:none;
													}
													.form-group.has-success .success-msg
													{
														display:block;
													}

													.input-group
													{
														max-width:200px;
													}
												}
											}
											.historial
											{
												.historial-row
												{
													display:flex;
													justify-content:space-between;

													> div
													{
														display:flex;
														align-items:center;
													}

													&.head
													{
														//display:none;
														font-weight:700;
														@media (max-width:@screen-sm-max)
														{
															display:flex;
														}
													}

													.remove
													{
														min-width:36px;

														button
														{
															margin-bottom:5px;
															margin-top:5px;
														}
													}

													.date
													{
														min-width:80px;
													}

													.series, .repetitions, .loads
													{
														width:33%;
														overflow:hidden;
														padding:0 4px;
													}
												}
											}
										}

										.more
										{
											padding-top:10px;
											padding-bottom:10px;
											align-self:center;

											display:flex;
											flex-wrap:wrap;
											justify-content:center;

											button
											{
												margin-top:5px;
												margin-bottom:5px;
											}

											@media (max-width:@screen-sm-max)
											{
												width:100%;
												padding-top:10px;
												padding-bottom:10px;
												flex-direction:row;
												justify-content:space-evenly;
											}

										}
									}

			
									.details
									{
										width:100%;

										.images
										{
											display:flex;
											justify-content:space-around;
											align-items:center;

											margin-bottom:20px;

											img
											{
												max-width:40%;
											}
										}

										.description
										{
											margin-bottom:20px;
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}

	.assing-training-row
	{
		background-color:#ddd;
		padding-top:20px;

		.col-new-training
		{
			display:none;

			.categories
			{
				.old-style-editor-categories();
			}

			textarea
			{
				width:100%;
				margin-bottom:20px;
			}
		}

		.col-existing-training
		{
			display:none;

			.select-environments-layer
			{
			}
			.select-environments-layer
			{
			}

			textarea
			{
				width:100%;
				margin-bottom:20px;
			}
		}
	}
}
