body.page
{
	.page-content
	{
		padding-bottom:@line-height-computed;

		iframe
		{
			width:100%;

		}

		.wp-embed-aspect-16-9
		{
			iframe
			{
				min-height:642px;
				@media (max-width:@screen-md-max) {min-height:529px;}
				@media (max-width:@screen-sm-max) {min-height:405px;}
				@media (max-width:@screen-xxs-max) {min-height:270px;}
			}
		}
	}
}
