.main-footer
{
 	//#gradient > .vertical(@start-color: @navbar-default-bg; @end-color: lighten( @brand-primary, 40%) );
	background-color:transparent;
	border:0;

	padding-top:50px;
	padding-bottom:50px;

	a
	{
		color:#b6c532;

		&:hover
		{
			color:#9babc1;
		}
	}

	.logo-link
	{
		img
		{
			width:100%;
		}
	}

	.footer-legal
	{
		.footer-legal-col
		{
			display:flex;
			justify-content:flex-start;
			align-items:center;

			img
			{
				width:136px;
				margin-right:20px;
				margin-bottom:10px;
			}

			@media (max-width:@screen-xxs-max)
			{
				flex-wrap:wrap;

				img
				{
					width:100%;
				}
			}
		}
	}
}
