.main-menu-navbar
{
	background-image:none;
	border:0;
	box-shadow:none;
	margin-bottom:0;

	min-height:0;

	@media (max-width:@grid-float-breakpoint-max)
	{
		position:fixed;
		left:0;
		top:118px;
		width:100%;
		z-index:@zindex-navbar;
	}

	> .container, > .container-fluid
	{
		background-color:transparent;
		padding:0;

		.navbar-collapse
		{
			padding:0;

			@media (max-width:@grid-float-breakpoint-max)
			{
				width:100%;
				margin:0;
			}

			.navbar-nav
			{
				@media (max-width:@grid-float-breakpoint-max)
				{
					margin:0;
				}

				a
				{
					padding:20px 15px;

					color:#636873;
					font-size:13px;
					text-transform:uppercase;

					&:hover
					{
						color:#b6c532;
						background-color:transparent;
					}
				}
			}
		}
	}
}
