.jlc-cookies-message-layer
{
	position: fixed;
    bottom: 0;
    width: 100%;
	z-index:(@zindex-modal-background - 1);
    text-align: center;
	color:@white;
    background-color: @brand-primary;
	#gradient .vertical(@brand-primary,darken(@brand-primary,10%));
    border-top: 1px solid lighten(@brand-primary,10%);
    box-shadow: 0 -3px 3px 0 rgba(0,0,0,.2);
    padding:(@line-height-computed / 2);
	overflow:hidden;

	@media (min-width:@grid-float-breakpoint)
	{
		display: flex;
		flex-wrap: nowrap;
		align-items: center;
		justify-content: space-evenly;
	}

	&.not-show
	{
		height:0;
		padding:0 (@line-height-computed / 2);
		border-top:none;
		box-shadow:none;
		@not-show-trainsition:height .3s ease-in, padding .3s ease-in, border .2s linear .1s;
		.transition(@not-show-trainsition);
	}

	p
	{
		font-size:18px;
		margin:0;

		a
		{
			color:@white;
			font-weight:bold;
		}
	}

	button
	{
		.btn-styles(@brand-secondary);
		font-size:18px;
		padding:6px 12px;
		min-width:100px;

		@media (max-width:@grid-float-breakpoint-max)
		{
			margin-top:(@line-height-computed / 2);
		}
	}
}
