.edit-section-button
{
	position:absolute;
	top:2px;
	right:2px;
	width:30px;
	height:30px;

	border-radius:100%;

	border:2px solid #eee;
	box-shadow:1px 1px 2px 0 #666;

	color:#000;
	background-color:#b6c532;

	animation-name: edit-section-button-lighting;
	animation-duration: 1s;
	animation-iteration-count: infinite;

	font-size:14px !important;
}

.main-options-layer
{
	position:fixed;
	bottom:20px;
	right:20px;

	.main-options-button
	{
		width:60px;
		height:60px;

		border-radius:100%;

		border:3px solid #eee;
		box-shadow:1px 1px 3px 0 #666;

		color:#000;
		background-color:#b6c532;
/*
		animation-name: edit-section-button-lighting;
		animation-duration: 1s;
		animation-iteration-count: infinite;
*/
	}
}

.page-bg-layer
