.submenu-navbar
{
	background-image:none;
	border:0;
	box-shadow:none;
	margin-bottom:0;

	min-height:0;

	> .container, > .container-fluid
	{
		background-color:transparent;

		.two-links-row
		{
			font-size:13px;
			display:flex;
			flex-wrap:wrap;

			.my-profile-col
			{
				background-color:#ddd;
				text-align:center;
				text-transform:uppercase;

				&:hover
				{
					background-color:#c3c3c3;
				}

				a
				{
					display:block;
					width:100%;
					color:#636873;
					padding-top:11px;
					padding-bottom:11px;
				}
			}

			.call-you-col
			{
				background-color:#b6c532;
				text-align:center;
				text-transform:uppercase;

				&:hover
				{
					background-color:#a1ae2f;
				}

				a
				{
					display:block;
					width:100%;
					color:#fff;
					padding-top:11px;
					padding-bottom:11px;
				}
			}
		}
	}
}
