#we-call-you-modal
{
	.modal-content
	{
		background-color: transparent;
		box-shadow: none;
		border: 0;

		.modal-header
		{
			background-image: linear-gradient(to top, #bfbdb9 0, #dadbd8 50%, #e6e7e5 100%);
			border-radius: 12px;
			box-shadow: 2px 2px 2px 0 #111;
			border: 2px solid #80817d;

		}

		.modal-body
		{
			padding-left:0;
			padding-right:0;
		}
	}

	form
	{
		width:100%;
		padding:(@line-height-computed / 2);

		background-image: linear-gradient(to top, #bfbdb9 0, #dadbd8 25%, #fdfdfc 50%, #dadbd8 75%, #e6e7e5 100%);
		//margin-top: 20px;
		//margin-bottom: 20px;
		border-radius: 12px;
		box-shadow: 2px 2px 2px 0 #111;
		border: 2px solid #80817d;

		label
		{
			color: #000;
			text-shadow: -1px -1px 0 #eeeeec,1px 1px 0 #666;
		}


		.form-control
		{
			box-shadow: inset 2px 2px 2px 0 #666;
			border-radius: 6px !important;
			background-color: green;
			border-style: solid;
			background-image: linear-gradient(to bottom, #9cc242 0, #d5d237 50%, #9cc242 100%);
			font-size: 20px;
			border-width: 1px;
			border-color: #eeeeec #c1bfbc #c5c4c0 #eeeeed;
		}

		textarea.form-control
		{
			height: 138px;
		}
		input.form-control
		{
			height: 46px;
		}

		a
		{
			color:@white;
			font-weight:bold;

			&:hover
			{
				//color:lighten(@brand-primary,30%);
			}
		}

		button
		{
			//margin-top:10px;
			//width:100%;
			//.btn-styles(@background-color-2);
			//color:@white;

			display:block;
			margin:@line-height-computed auto;
			font-size:24px;
			//.btn-styles(#21b72e);
			border-color: #666;
			border-radius: 12px;
			height: 50px;
			padding-left: 20px;
			padding-right: 20px;
			background-image: linear-gradient(to top, #bfbdb9 0, #dadbd8 25%, #fdfdfc 50%, #dadbd8 75%, #e6e7e5 100%);
			color: #a1c340;
			text-shadow: 1px 1px 0 #80817d, -1px -1px 0 #fafaf8;
			font-weight: bold;

			&:hover, &:focus, &:active
			{
				//color:@white;
			}
		}

	}
}
