.cases-container
{
	margin-bottom:40px;
	overflow:hidden;

	@media (max-width:@screen-xxs-max)
	{
		margin-bottom:10px;
	}

	> .row
	{
		display:flex;
		flex-wrap:wrap;

		.photo
		{
			padding:0;

			display:flex;
			
			min-height:400px;

			@media (max-width:@screen-xs-max)
			{
				order:1;
			}

			img
			{
				width:100%;
			}

			.real-case-photo
			{
				width:50%;

				background-size:cover;
				background-position:50% 50%;

				position:relative;
			}

			@media (max-width:@grid-float-breakpoint-max) {order:1;}
		}

		.text
		{
			display:flex;
			flex-direction:column;
			align-items:center;
			justify-content:center;

			@media (max-width:@screen-xs-max)
			{
				order:2;
			}

			h3
			{
				width:100%;
			}

			.inner-text
			{
				width:100%;
			}

			@media (max-width:@grid-float-breakpoint-max) {order:2;}
		}

		&.case-odd
		{
			.real-case-photo
			{
				&.real-case-photo-before
				{
					left:-100%;
				}
				&.real-case-photo-after
				{
					left:-200%;
				}
			}

			&.show
			{
				display:flex !important;

				.real-case-photo
				{
					&.real-case-photo-before
					{
						left:0;
						transition:left 1.5s ease-out;
					}
					&.real-case-photo-after
					{
						left:0;
						transition:left 1.5s ease-out;
					}
				}
			}
		}
		&.case-even
		{
			.real-case-photo
			{
				&.real-case-photo-before
				{
					right:-100%;
				}
				&.real-case-photo-after
				{
					right:-200%;
				}
			}

			&.show
			{
				display:flex !important;

				.real-case-photo
				{
					&.real-case-photo-before
					{
						right:0;
						transition:right 1.5s ease-out;
					}
					&.real-case-photo-after
					{
						right:0;
						transition:right 1.5s ease-out;
					}
				}
			}
		}
	}
}

.more-cases-container
{
	position:relative;
	text-align:center;

	.more-cases-link
	{
					display:inline-block;
					background-color:#2c3343;
					//color:#fff;
					font-size:14px;
					padding: 10px 20px;
					border-radius: 20px;

					margin-bottom:40px;

					&:hover
					{
						background-color:#3c465c;
					}

					@media (max-width:@screen-xs-max)
					{
						display:inline-block;
					}

					@media (max-width:@screen-xxs-max)
					{
						margin-bottom:0;
					}
	}

}

.archive-cases-container
{
	.video-layer
	{
		position:relative;

		.video
		{
			position:relative;
			width:100%;
			padding-bottom:56.25%;
			
			iframe
			{
				position:absolute;
				top:0;
				left:0;
				width:100%;
				height:100%;
			}
		}

	}
}
