.plans-container
{
	margin-bottom:40px;

	> .row
	{
		.col-xs-12
		{
			text-align:center;

			.price
			{
				position:relative;

				font-size:120px;

				small
				{
					font-size:30px;
					vertical-align:top;

					font-size: 30px;

					position: absolute;
					left: -120px;
					bottom: 23%;
					display: block;
					top: auto;
					height: auto;
				}
				sup
				{
					font-size:30px;

					position: absolute;
					right: -20px;
					bottom: 30%;
					display: block;
					top: auto;
					height: auto;
				}


				@media (max-width:@screen-xs-max)
				{
					font-size:40px !important;

					small
					{
						font-size: 20px;
					}
					sup
					{
						font-size: 20px;
					}
					.edit-section-button
					{
						right:-30px;
						top:-20px;
					}
				}

			
			}

			.type
			{
				font-size:30px;
				color:@brand-primary;

				position:relative;

				.edit-section-button
				{
					right:-40px;
				}
			}

			.desc
			{
				position:relative;
				font-size:24px;

				.edit-section-button
				{
					right:-30px;
					top:-5px;
				}
			}

			.features
			{
				position:relative;

				margin-top:40px;
				margin-bottom:40px;

				font-size:16px;
			}

			.know-more-layer
			{
				position:relative;

				.know-more
				{
					padding:20px;
					background-color:@brand-primary;
					color:#fff;

					border-radius:60px;

					display:inline-block;
					margin-bottom:20px;
				}
			}

		}
	}
}
