// Separate navbar from top a distance equals to Wordpress admin bar height.
body.admin-bar .nav.navbar-fixed-top
{
	top:32px;

	@media (max-width:782px)
	{
		top:46px;
	}
}
body.admin-bar .navbar.navbar-fixed-top
{
	top:(32px + @top-navbar-height);

	@media (max-width:782px)
	{
		top:(46px + @top-navbar-height);
	}
}

// Admin bar always fixed top
#wpadminbar
{
	@media screen and (max-width: 600px)
	{
		position:fixed;
	}
}

// Separate modal windos from mobile admin bar
body.admin-bar .modal-dialog
{
	@media (max-width:@screen-xs-max)
	{
		margin-top:53px;
	}
}
