.move-image-arrow
{
	position:absolute;
	width:40px;
	height:40px;
	z-index:(@zindex-modal-background - 1);

	border-radius:100%;

	border:2px solid #eee;
	box-shadow:1px 1px 2px 0 #666;

	color:#000;
	background-color:#b6c532;

	animation-name: edit-section-button-lighting;
	animation-duration: 1s;
	animation-iteration-count: infinite;

	font-size:20px !important;

	&.move-image-arrow-top
	{
		top:2px;
		left:50%;
		transform:translate(-50%,0);
	}

	&.move-image-arrow-right
	{
		top:50%;
		right:2px;
		transform:translate(0,-50%);
	}

	&.move-image-arrow-left
	{
		top:50%;
		left:2px;
		transform:translate(0,-50%);
	}

	&.move-image-arrow-bottom
	{
		bottom:2px;
		left:50%;
		transform:translate(-50%,0);
	}
}

