@keyframes home-cover-animation
{
	0%
	{
		transform:scale(1);
	}
	100%
	{
		transform:scale(1.05);
	}
}

.home-cover
{
	margin-bottom:20px;

	.home-cover-col
	{
		position:relative;

		padding-top:20%;
		padding-bottom:20%;

		overflow:hidden;

		.home-cover-col-bg
		{
			position:absolute;
			top:0;
			left:0;
			width:100%;
			height:100%;

			background-image:url('../img/photos/default_home_cover.jpg');
			background-position:50% 50%;
			background-size:cover;
/*
			animation-name:home-cover-animation;
			animation-duration:20s;
			animation-fill-mode:forwards;
			animation-iteration-count: infinite;
*/			

			@media (orientation:portrait)
			{
				background-image:url('../img/photos/default_home_cover.jpg');
			}
		}

		.sup
		{
			position:relative;

			width:60%;
			padding-left:15px;
			padding-right:15px;

			@media (max-width:@screen-xs-max)
			{
				width:100%;
			}

			.title-layer
			{
				position:relative;
			
				.title
				{
					text-align:center;

					//color:#fff;
					border:3px solid #fff;
					padding:20px 10px;
					margin:15px 0 18px 0;

					font-size:36px;
					line-height:42px;

					background-color:fade(#000,43%);
				}
			}
		}

		.left
		{
			position:relative;

			width:50%;
			padding-left:15px;
			padding-right:15px;

			float:left;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: flex-start;

			@media (max-width:@screen-xs-max)
			{
				width:100%;
			}


			.text
			{
				position:relative;
				width:100%;

				padding-top:20px;

				margin-bottom:10px;
				//color:#fff;

				font-size:20px;
				line-height:26px;

				color:#fff;
			}

			.join-link-layer
			{
				position:relative;

				@media (max-width:@screen-xs-max)
				{
					margin-bottom:20px;
				}

				.join-link
				{
					display:inline-block;
					background-color:#2c3343;
					//color:#fff;
					font-size:14px;
					padding: 10px 20px;
					border-radius: 20px;

					&:hover
					{
						background-color:#3c465c;
					}

					@media (max-width:@screen-xs-max)
					{
						display:inline-block;
					}
				}
			}
		}

		.right
		{
			position:relative;

			width:50%;
			padding-left:15px;
			padding-right:15px;

			float:left;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;

			@media (max-width:@screen-xs-max)
			{
				width:100%;
			}

			.video-title
			{
				position:relative;

				h3
				{
					//color:#fff;
				}

				.edit-section-button
				{
					right:-30px;
				}
			}


			.video-layer
			{
				position:relative;

				.video
				{
					iframe
					{
						max-width:100%;
					}
				}
			}
		}
	}
}
