body.page-trainer-diet-list
{
	table.items-table
	{
		tr.diet-template.new-duplicated
		{
			background-color:lighten(@brand-primary,10%);
		}

		.diet-template
		{
			&.diet-inactive
			{
				opacity:.5;
			}
		}
	}

	// CONFIRMATIONS
	table.items-table > tbody > tr > td
	{

		a.confirm-diet-duplication
		{
			display:none;
		}
		a.duplicate-diet
		{
		}


		a.confirm-diet-deletion
		{
			display:none;
		}
		a.delete-diet
		{
		}


		a.confirm-diet-edition
		{
			display:none;
		}
		a.edit-diet
		{
		}

	}

	&.must-confirm-diet-duplication
	{
		table.items-table > tbody > tr > td
		{
			a.confirm-diet-duplication
			{
				display:inline-block;
			}
			a.duplicate-diet
			{
				display:none;
			}
		}
	}
	&.must-confirm-diet-deletion
	{
		table.items-table > tbody > tr > td
		{
			a.confirm-diet-deletion
			{
				display:inline-block;
			}
			a.delete-diet
			{
				display:none;
			}
		}
	}
	&.must-confirm-diet-edition
	{
		table.items-table > tbody > tr > td
		{
			a.confirm-diet-edition
			{
				display:inline-block;
			}
			a.edit-diet
			{
				display:none;
			}
		}
	}

	// TRAINING TOOLBAR

	.diet-tools
	{
		.create-diet
		{
			.text
			{
				@media (max-width:@screen-xs-max)
				{
					//display:none;
				}
			}
			.glyphicon
			{
				@media (min-width:@screen-sm-min)
				{
					display:none;
				}
			}
		}
	}

	// FILTERS

	.diet-filters-toggler-layer
	{
		&.one-row
		{
			align-self:center;

			@media (max-width:@screen-sm-max)
			{
				display:none;
			}
		}
		&.multi-row
		{
			@media (min-width:@screen-md-min)
			{
				display:none;
			}
		}

		.diet-filters-toggler
		{

			.text
			{
				@media (max-width:@screen-sm-max)
				{
					display:none;
				}

				&.show-filters
				{
				}
				&.hide-filters
				{
					display:none;
				}
			}
			.glyphicon
			{
				@media (min-width:@screen-md-min)
				{
					display:none;
				}
			}

			&.on
			{
				.text
				{
					&.show-filters
					{
						display:none;
					}
					&.hide-filters
					{
						display:inline;

						@media (max-width:@screen-sm-max)
						{
							display:none;
						}
					}
				}
			}
		}
	}

	.diet-filters
	{
		display:flex;
		justify-content:flex-end;
		align-items:stretch;
		max-width:50%;

		@media (max-width:@screen-sm-max)
		{
			width: 100%;
			max-width: 100%;
			flex-wrap:wrap;
			justify-content:space-between;
		}

		.diet-filter
		{
			display:none;

			width:50%;

			border:1px solid #aaa;
			border-radius:5px;
			padding:5px 5px 0 5px;
			margin-left:5px;

			@media (max-width:@screen-sm-max)
			{
				margin-top:10px;
				width:49.5%;
				margin-left:0;
			}

			@media (max-width:@screen-xxs-max)
			{
				width:100%;
			}

			.checkboxes-layer
			{
				display:flex;
				flex-wrap:wrap;
				justify-content:space-between;

				.checkbox
				{
					min-width:33%;
					margin-bottom:10px;
					margin-top:0;
				}

				.checkbox + .checkbox
				{
					margin-top:0;
				}
			}
		}

		&.on
		{

			.diet-filter
			{
				display:block;
			}
		}
	}
}
