.old-style-editor-categories()
{
	
	.exercise-category
	{
		display:flex;
		align-items:flex-start;

		border:1px solid #aaa;
		border-radius:2px;
		padding:2px;
		
		@media (max-width:@screen-xs-max)
		{
			flex-wrap:wrap;
		}

		&.hovering
		{
			//padding-bottom:20px;
			//background-color:yellow;
			margin-bottom:40px;
		}

		.name
		{
			width:20%;
			display:flex;

			@media (max-width:@screen-xs-max)
			{
				width:100%;
			}

			.dragger
			{
				padding:7px 10px 0 10px;
				cursor:hand;
			}

			.text
			{
				padding:7px 4px 0 4px;
			}
		}

		.exercises
		{
			width:80%;
			
			@media (max-width:@screen-xs-max)
			{
				width:100%;
			}

			.exercise
			{
				display:flex;
				@media (max-width:@screen-xs-max)
				{
					flex-wrap:wrap;
					justify-content:flex-end;
				}

				.remove-layer
				{
					
				}	

				.form-group
				{
					margin-bottom:0;
				}

				.exercise-type-form-group
				{
					width:230px;
				}
		
				.select-form-group
				{
					width:100%;

					.exercise-select-input
					{
						display:none;

						&.active-selector
						{
							display:block;
						}
					}
				}

				.input-form-group
				{
					min-width:80px;

					@media (max-width:@screen-xs-max)
					{
						width:33%;
					}
				}
			}
		}

		.exercise-template
		{
			display:none;
		}
	}
}
