.user-navbar
{
	background-image:none;
	border:0;
	box-shadow:none;
	margin-bottom:0;

	min-height:0;

	> .container, > .container-fluid
	{
		background-color:transparent;

		.user-menu-col
		{
			font-size:13px;
			padding:0;

			.user-links
			{
				img
				{
					width:40px;
					height:40px;
				}

				.user-name
				{
					color:#636873;
					font-size:14px;
				}

				.user-menu-button
				{
					color:#636873;
					background-color:#ddd;

					position: relative;
					padding: 12px 10px;
					background-color: #ddd;
					border:0;
					margin-left:10px;

					&:hover
					{
						background-color:#c3c3c3;
					}

					.icon-bar
					{
						background-color:#636873;
						display: block;
						width: 22px;
						height: 2px;
						border-radius: 1px;

						& + .icon-bar
						{
							margin-top:4px;
						}
					}
				}

			}

			.we-call-you-layer
			{
				float:right;

				background-color:#b6c532;

				text-transform:uppercase;

				&:hover
				{
					background-color:#a1ae2f;
				}

				a
				{
					padding:11px;
					color:#fff;
					display:block;
				}
			}
		}
	}
}

