input[type="radio"].cool {
    //display:none;

	border: 0 !important;
	clip: rect(0 0 0 0) !important;
	-webkit-clip-path: inset(50%) !important;
	clip-path: inset(50%) !important;
	height: 1px !important;
	overflow: hidden !important;
	padding: 0 !important;
	position: absolute !important;
	width: 1px !important;
	white-space: nowrap !important;

	left:30px;
	top:0;
}
input[type="radio"].cool + label {
    color: darken(@brand-primary,30%);
    font-size:14px;
}
input[type="radio"].cool + label span.checker {
    display:inline-block;
    width:19px;
    height:19px;
    margin:-1px 4px 0 0;
    vertical-align:middle;
    cursor:pointer;
    border-radius:  50%;
	position:relative;
}

input[type="radio"].cool + label span.checker {
     background-color: lighten(@brand-primary,10%);
	box-shadow:inset 0 0 2px 1px #000;

	&:after
	{
		position:absolute;
		content:'';
		display:none;
	}
}

input[type="radio"].cool:checked + label span.checker{
     //background-color:lighten(@brand-primary,10%);
     background-color:@brand-primary;
	box-shadow:0 0 0 1px lighten(@brand-primary,25%), 0 0 2px 1px lighten(@brand-primary,15%);

	&:after
	{
		display:block;
		top:35%;
		left:35%;
		width:30%;
		height:30%;
		border-radius: 50%;
		background-color:lighten(@brand-primary,20%);
		box-shadow:-1px -1px 1px 0 darken(@brand-primary,10%),1px 1px 1px 0 darken(@brand-primary,30%);
	}
}

input[type="radio"].cool + label span.checker,
input[type="radio"].cool:checked + label span.checker {
  -webkit-transition:background-color 0.4s linear;
  -o-transition:background-color 0.4s linear;
  -moz-transition:background-color 0.4s linear;
  transition:background-color 0.4s linear;
}

