.target-container
{
	background-image:url('../img/photos/banner-servicios-2.jpg');
	background-size:cover;
	background-position:50% 50%;

	margin-bottom:40px;

	.target-col
	{
		display:flex;
		justify-content:flex-end;
		align-items:center;

		.content
		{
			width:40%;

			//color:#fff;

			margin-top:100px;
			margin-bottom:100px;

			@media (max-width:@screen-xs-max)
			{
				width:100%;
			}

			.target-title-layer
			{
				position:relative;
			}

			.inner-text
			{
				position:relative;
				margin-top:20px;
				margin-bottom:20px;
				padding:10px 0;
			}

			.target-link-layer
			{
				position:relative;

				@media (max-width:@screen-xs-max)
				{
					text-align:center;
				}

				a
				{
					display:inline-block;

					background-color:#2c3343;
					color:#fff;
					font-size:14px;
					padding: 10px 20px;
					border-radius: 20px;

					&:hover
					{
						background-color:#3c465c;
					}
				}
			}
		}
	}
}
