.basic-contact-layer()
{
	//background-color:#fff;
	//background-repeat:no-repeat;
	//background-position:50% 0;
	
	//box-shadow:0 -2px 4px 0 @gray;


	& > .row
	{
		//background-color:@background-color-2;
		background-color:@brand-primary;
		#gradient .vertical(darken(@brand-primary,8%),@brand-primary);

	padding-top:40px;
	padding-bottom:40px;

		& > .col-xs-12
		{
		//	padding-bottom:@line-height-computed;
		}
	}

	.contact-flex-row
	{
		display:flex;
		flex-wrap:wrap;

		.contact
		{
			display:flex;
			flex-direction:column;
			align-items:center;
			justify-content:center;

			@media (max-width:@screen-xs-max)
			{
				order:2;
				padding:0;
			}
		}

		.img
		{
			display:flex;
			align-items:flex-end;
			justify-content:center;

			@media (max-width:@screen-xs-max)
			{
				img
				{
					width:100%;
					max-width:300px;
					margin-bottom:(@line-height-computed / 2);
				}

				order:1;
			}
		}
	}

	h1,h2,h3
	{
		//color:@white;
		//text-shadow:1px 1px 0 #aa8f7d; 
	}

	.contact-form-layer
	{
		position:relative;
	}

	form
	{
		width:100%;
		padding:(@line-height-computed / 2);
		background-color:transparent;
		//box-shadow:2px 2px 0 0 @brand-secondary;

		label
		{
			color:@white;
			//text-shadow:1px 1px 0 #aa8f7d; 
		}

		input, textarea
		{
			border-radius:25px;
			
		}
		textarea
		{
			height:80px;
		}

		a
		{
			color:@white;
			font-weight:bold;

			&:hover
			{
				//color:lighten(@brand-primary,30%);
			}
		}

		button
		{
			//margin-top:10px;
			//width:100%;
			//.btn-styles(@background-color-2);
			//color:@white;

			display:block;
			margin:@line-height-computed auto;
			font-size:24px;
			//.btn-styles(#21b72e);

			&:hover, &:focus, &:active
			{
				//color:@white;
			}
		}

	}

	.map-col
	{
		padding:0;
		
		iframe
		{
			width:100%;
		}
	}

	.link
	{
		text-align:center;

		a
		{
			display:inline-block;

			padding:10px;

			background-color:#fff;
			color:@brand-primary;

			//height:30px;
			border-radius:30px;
			font-size:16px;

			@media (max-width:@screen-xs-max)
			{
				height:auto;
			}
		}
	}

	.tel
	{
		text-align:center;

		color:#fff;
		font-size:16px;

		margin-top:20px;
		margin-bottom:20px;
	}
	
}

.contact-container
{
	.basic-contact-layer();

	position:relative;
}


