.user-fixed-layer
{
	position:fixed;
	bottom:20px;
	left:20px;
	z-index: (@zindex-navbar-fixed + 1);

	.user-menu-button,
	.trainer-edit-button,
	.trainer-help-link
	{
		display:inline-block;

		width:60px;
		height:60px;

		border-radius:100%;

		border:3px solid #eee;
		box-shadow:1px 1px 3px 0 #666;

		color:#000;
		background-color:#b6c532;
	}
}

.trainer-vertical-menu
{
	position:fixed;
	top:0;
	left:0;
	width:0;
	height:100vh;
	max-width:270px;

	z-index: (@zindex-navbar-fixed + 2);

	overflow:auto;

	background-color:#242a37;

	&.open
	{
		width:100%;
	}

	.close-menu
	{
		position:absolute;
		right:0;
		top:0;

		width:20px;
		height:20px;

		background-color:#b6c532;
		color:#fff;
	}

	.trainer-vertical-menu-inner
	{
		.user-info
		{
			img
			{
				float:left;
				width:80px;
				height:80px;
				border-radius:100%;
				margin-right:10px;
				margin-bottom:10px;
			}

			.user-name
			{
				font-size:14px;
				color:rgb(99, 104, 115);
			}
			.user-mail
			{
				font-size:13px;
				color:@brand-primary;
			}
			.close-session
			{
				font-size:13px;
				a
				{
					color:@brand-primary;
				}
			}

			
		}
	
		ul
		{
			list-style:none;
			padding:0;

			li
			{
				a
				{
					display:block;
					width:100%;
					padding:10px 15px;

					color:#a4a8b1;

					&:hover
					{
						color:#b6c532;
						background-color:fade(#000,30%);
					}
				}
			}
		}
	}
}

body.unscrollable
{
	overflow:hidden;
}
