body.page-trainer-exercises-list
{
	.tools-bar
	{
		@media (max-width:@screen-xs-max)
		{
			.exercise-filters
			{
				widht:100%;

				.exercise-filter
				{
					display: flex;
					align-items: center;
					justify-content: flex-end;
					flex-wrap:wrap;

					.form-group
					{
						display:flex;
						align-items:center;
						margin-bottom:5px;

						label,select
						{
							margin:0 5px 0 0;
						}

					}
				}
			}

		}
		.exercise-tools
		{
			display:flex;
			width:100%;

			@media (max-width:@screen-xs-max)
			{
				flex-wrap:wrap;
			}

			> a.btn
			{
				@media (max-width:@screen-xs-max)
				{
					width:100%;
				}
			}
		}
	}

	table.items-table > tbody > tr
	{

		&.new-duplicated
		{
			background-color:lighten(@brand-primary,10%);
		}

		a.confirm-duplication
		{
			display:none;
		}
		a.duplicate-exercise
		{
		}

		a.confirm-deletion
		{
			display:none;
		}
		a.delete-exercise
		{
		}


		a.confirm-edition
		{
			display:none;
		}
		a.edit-exercise
		{
		}

	}

	&.must-confirm-duplication
	{
		table.items-table > tbody > tr
		{
			a.confirm-duplication
			{
				display:inline-block;
			}
			a.duplicate-exercise
			{
				display:none;
			}
		}
	}

	&.must-confirm-deletion
	{
		table.items-table > tbody > tr
		{
			a.confirm-deletion
			{
				display:inline-block;
			}
			a.delete-exercise
			{
				display:none;
			}
		}
	}

	&.must-confirm-edition
	{
		table.items-table > tbody > tr
		{
			a.confirm-edition
			{
				display:inline-block;
			}
			a.edit-exercise
			{
				display:none;
			}
		}
	}
}

