.cart-fixed
{
	position:fixed;
	bottom:25%;
	right:0;
	width:53px;
	z-index:(@zindex-navbar-fixed + 1);

	background-color:darken(@brand-primary,10%);
	border:1px solid @brand-secondary;
	border-right-width:0;
	border-radius:8px 0 0 8px;

	padding:14px 6px 6px 6px;

	box-shadow:-1px 1px 2px 0 fade(@gray-base,50%);

	.transition(all 1s ease);

	a.cart-button
	{
		display: block;
		margin:0;
		font-size: 24px;
		color: @white;
		text-align: center;
		line-height: 1;
		text-shadow:1px 1px 0 #aaa, -1px -1px 0 #666;

		&:hover
		{
			color:lighten(@brand-primary,20%);
		}

		.cart-count
		{
			display: block;
			position: absolute;
			right: 5px;
			top: -8px;
			font-size: 14px;
			font-weight:bold;
			background-color: @white;
			color:darken(@brand-primary,20%);
			padding: 4px 8px;
			border-radius: 4px;
			box-shadow: 1px 1px 0 0 #666, -1px -1px 0 0 #aaa;
			text-shadow: none;

		}
	}


	&.not-show
	{
		width:0;
		right:-20px;
		box-shadow:0 0 2px 0 fade(@brand-primary,50%);
		.transition(all 1s ease);

		a.cart-button
		{
			.cart-count
			{
				display:none;
			}
		}
	}

}

