.header-navbar
{
	background-image:none;
	background-color:transparent;
	border:0;
	box-shadow:none;
	margin-bottom:0;

	border-radius:0;

	> .container, > .container-fluid
	{
		position:relative;
		background-color:transparent;//#000;

		.navbar-header
		{
			position:relative;
			@media (min-width:@screen-sm-min)
			{
				float:left;
			}
			// Para movil horizontal
			@media (max-width:@screen-sm-max) and (orientation:landscape)
			{
				float:left;
			}

			.navbar-brand
			{
				padding:5px 35px;

				display:flex;
				justify-content:flex-start;
				align-items:center;

				@media (max-width:@screen-xs-max) and (orientation:portrait)
				{
					flex-direction:column;
					width:100%;
					height:auto;
				}

				.logo
				{
					margin-right:10px;

					@media (max-width:@screen-xs-max) and (orientation:portrait)
					{
						margin-right:0;
						margin-bottom:10px;
					}

					img
					{
						height:100px;
						width:auto;
						max-width:200px;

						@media (max-width:@screen-xs-max) and (orientation:portrait)
						{
							height:40px;
						}
					}
				}

				.text
				{
					color:#fff;

					@media (max-width:@screen-xs-max) and (orientation:landscape)
					{
						max-width:200px;
					}

					.site-title
					{
						font-size:25px;
					}
					.site-description
					{
						font-size:12px;

						@media (max-width:@screen-xs-max) and (orientation:portrait)
						{
							display:none;
						}
					}

					&:hover
					{
						text-shadow: 1px 1px 10px @brand-primary,5px 5px 10px @brand-primary,-5px -5px 10px @brand-primary;
					}
				}
			}

			.navbar-toggle
			{
				margin: 0;
				position: absolute;
				top: 5px;
				left: 1px;
			}
		}

		.phone-layer
		{
			position:relative;

			float:right;

			font-size:30px;

			padding-top:30px;
			padding-right:35px;

			@media (max-width:@screen-sm-max)
			{
				font-size:22px;
			}
			@media (max-width:@screen-xs-max) and (orientation:portrait)
			{
				padding:2px 15px;

				font-size:15px;

				float:none;

				width:100%;
				text-align:center;
			}

			a
			{
				color:#fff;

				.fa
				{
					color:#22B40C;
					font-size:50px;
					vertical-align:middle;

					@media (max-width:@screen-sm-max)
					{
						font-size:40px;
					}
					@media (max-width:@screen-xs-max) and (orientation:portrait)
					{
						font-size:24px;
					}
				}
			}


			.edit-section-button
			{
				top:20px;
				right:5px;

				@media (max-width:@screen-xs-max) and (orientation:portrait)
				{
					right:30%;
					top:-7px;
				}
				@media (max-width:@screen-xxs-max) and (orientation:portrait)
				{
					right:20%;
				}
			}

			.navbar-toggle
			{
				margin: 4px 0 0 0;
				display:none;
				float:left;
			}
		}
	}
