.widget-area
{
	.widget
	{
		margin-bottom:@line-height-computed;
	}

	.widget_recent_entries
	{
		color:@brand-primary;
		font-family:'OdinRounded';
		font-size:22px;
		text-transform:uppercase;
		
		ul
		{
			position:relative;
			&:after
			{
				background: linear-gradient(to bottom, @brand-primary 50%,@brand-secondary 50%, @brand-secondary 100%);
				position: absolute;
				content: '';
				height: 8px;
				left: 0;
				width:100%;
				top: -8px;
			}

			font-family:@font-family-sans-serif;
			list-style:none;
			padding:( @line-height-computed / 2 ) 0 0 0;
			margin-top:(@line-height-computed / 2);
			
			li
			{
				font-size:18px;
				margin-bottom:(@line-height-computed / 2);
				text-transform:none;
			}
		}
	}

}
