body.page-trainer-edit-member
{
	h2,h3,h4
	{
		margin-top:0;
	}

	.shadow-box
	{
		box-shadow:1px 1px 2px 1px #666;
		padding:10px;
		border-radius:10px;
		margin-bottom:20px;

		.btn
		{
			white-space:normal;
			text-align:left;
		}
	}

	.main-info
	{
		.info
		{
			display:none;
			align-items:flex-start;

			@media (max-width:@screen-xs-max)
			{
				flex-wrap:wrap;
				justify-content:space-between;
			}

			.front-photo, .side-photo
			{
				width:30%;
				margin-right:1%;
				padding:5px;
				border-radius:5px;
				box-shadow:1px 1px 2px 0 #888;

				img
				{
					width:100%;
					height:auto;
				}

				@media (max-width:@screen-xs-max)
				{
					width:49%;
				}
			}

			.text
			{
				width:38%;
				align-self:stretch;

				display:flex;
				flex-direction:column;
				justify-content:space-between;

				@media (max-width:@screen-xs-max)
				{
					width:100%;
					padding-top:10px;
				}
			}
		}
		.edit-info
		{
		}

		&.read
		{
			.info { display:flex; }
			.edit-info {display:none;}
		}
	}

	.bars
	{
		.bar
		{
			position:relative;

			width:100%;

			margin-bottom:10px;

			border-radius:2px;
			height:10px;

			box-shadow:inset 0 1px 2px 0 #000;

			.inner
			{
				background-color:@brand-primary;
				height:10px;
				box-shadow:0 0 3px 0 lighten(@brand-primary, 5%);
			}
		}
	}
}
