body.page-trainer-food-list
{
	.food-filters
	{
		width:100%;

		.food-filter
		{
			width:100%;
			display:flex;
			@media (max-width:@screen-xs-max)
			{
				flex-wrap:wrap;
			}

			.form-group
			{
				width:100%;

				@media (max-width:@screen-xs-max)
				{
					margin-bottom:5px;
				}

				a.btn
				{
					width:100%;
				}
			}
		}
	}

	.new-duplicated
	{
		background-color:@brand-primary !important;
	}
}
