input[type="checkbox"].cool {

	border: 0 !important;
	clip: rect(0 0 0 0) !important;
	-webkit-clip-path: inset(50%) !important;
	clip-path: inset(50%) !important;
	height: 1px !important;
	overflow: hidden !important;
	padding: 0 !important;
	position: absolute !important;
	width: 1px !important;
	white-space: nowrap !important;

	left:30px;
	top:0;
}
input[type="checkbox"].cool + label {
	padding-left:0;
}
input[type="checkbox"].cool + label span.checker {
    display:inline-block;
    width:19px;
    height:19px;
    margin:-1px 4px 0 0;
    vertical-align:middle;
    cursor:pointer;
	position:relative;
}

input[type="checkbox"].cool + label span.checker {
     background-color: lighten(@brand-primary,10%);
	box-shadow:inset 0 0 2px 1px #000;
	border-radius:4px;

	&:after
	{
		position:absolute;
		content:'';
		display:none;
	}
}

input[type="checkbox"].cool:checked + label span.checker{
     //background-color:lighten(@brand-primary,10%);
     background-color:@brand-primary;
	box-shadow:0 0 0 1px lighten(@brand-primary,22%), 0 0 2px 1px lighten(@brand-primary,15%);

	&:after
	{
		display:block;
		top: 20%;
		left: 8px;
		width: 4px;
		height: 60%;
		transform: rotate(45deg);
		border: solid #fff;
		border-width: 0 2px 2px 0;
		box-shadow:1px 1px 1px 0 darken(@brand-primary,20%);
	}
}

input[type="checkbox"].cool.required-warning + label span.checker{
	box-shadow:0 0 2px 1px #f00;
}

input[type="checkbox"].cool + label span.checker,
input[type="checkbox"].cool:checked + label span.checker {
  -webkit-transition:background-color 0.4s linear;
  -o-transition:background-color 0.4s linear;
  -moz-transition:background-color 0.4s linear;
  transition:background-color 0.4s linear;
}
