.presencial-plans-container
{
	.plan-row
	{

		.valoration
		{
			background-image:url('../img/star.png');
			background-repeat:repeat-x;
			background-size:24px 24px;

			height:24px;
			width:0;

			&[data-valoration="1"] {width:24px;}
			&[data-valoration="2"] {width:48px;}
			&[data-valoration="3"] {width:72px;}
			&[data-valoration="4"] {width:96px;}
			&[data-valoration="5"] {width:120px;}
		}
		

	}
}
