body.page-food-questionnaire
{
	.food-questionnaire-layer
	{
		.form-inner-layer
		{
			display:flex;
			flex-wrap:wrap;

			.column
			{
				width:25%;
				padding-left:5px;
				padding-right:5px;

				@media (max-width:@screen-md-max)
				{
					width:50%;
				}

				@media (max-width:@screen-xxs-max)
				{
					width:100%;
				}

				.cat-title
				{
					background-color:#000;
					color:#fff;
					font-weight:bold;
					text-align:center;
					padding:12px;
				}

				.food-row
				{
					padding:5px;

					display:flex;
					align-items:center;

					span
					{
						flex-grow:2;
					}

					select
					{
						width:80px;
					}

					label
					{
						display:none;
					}

					.form-group
					{
						margin:0;
					}
				}
			}
		}
	}
}

