body.page-trainer-help
{
	.help-container
	{
		h2,h3
		{
			color:@brand-primary;
		}

		a
		{
			color:#444;

			&:hover
			{
				color:@brand-primary;
			}
		}

		.index-row
		{
			h2,h3
			{
				@media (max-width:@screen-xs-max)
				{
					text-align:center;
					margin-bottom:30px;
				}
			}
			h2
			{
				@media (max-width:@screen-xs-max)
				{
					font-size:24px;
				}
			}
			h3
			{
				@media (max-width:@screen-xs-max)
				{
					font-size:20px;
				}
			}

			ul, ol
			{
				@media (max-width:@screen-xs-max)
				{
					text-align:center;
				}

				li
				{
					font-size:18px;
					margin-bottom:12px;
				}
			}
		}
	}

	.video-layer-y
	{
		iframe
		{
			display:block;
			margin-left:auto;
			margin-right:auto;

			max-width:100%;

			@media (max-width:520px)
			{
				width:300px;
				height:534px;
			}

			@media (max-width:370px)
			{
				width:268px;
				height:477px;
			}

			&.elite-h-video
			{
				width:560px;
				height:315px;

				@media (max-width:600px)
				{
					width:400px;
					height:225px;
				}

				@media (max-width:450px)
				{
					width:268px;
					height:151px;
				}
			}
		}
	}
