.old-style-editor-container
{
	.edit-training-row
	{
		background-color:#ddd;
		padding-top:20px;

		.col-new-training
		{

			.categories
			{
				.old-style-editor-categories();
			}

			textarea
			{
				width:100%;
				margin-bottom:20px;
			}
		}
	}
}

// ESTO DEBERIA SER TODO PARA LO ANTIGUO
.training-exercises-editor
{
	border:1px solid #000;
	padding:5px;

	.exercises-list
	{
		.cat-layer
		{
			display:flex;

			border:1px solid #999;
			padding:2px;
			margin-bottom:10px;

			.cat-position-controls
			{
				width:30px;
				padding-right:5px;

				.move
				{
					width:100%;
					background-color:#666;
					color:@brand-primary;
					border:3px solid @brand-primary;
					border-radius:10px;

					margin-bottom:5px;
					padding-left:3px;
					line-height:1;
					box-shadow:1px 1px 1px 0 #888, -1px -1px 1px 0 lighten(@brand-primary,20%);

					cursor:pointer;
				}
			}

			> .content
			{
				width:100%;

				.exercise
				{
					display:flex;

					border:1px solid #666;
					padding:2px;
					margin-bottom:5px;

					.exercise-position-controls
					{
						width:30px;
						padding-right:5px;

						.move
						{
							width:100%;
							background-color:#666;
							color:@brand-primary;
							border:3px solid @brand-primary;
							border-radius:10px;

							margin-bottom:5px;
							padding-left:3px;
							line-height:1;
							box-shadow:1px 1px 1px 0 #888, -1px -1px 1px 0 lighten(@brand-primary,20%);


							cursor:pointer;
						}
					}

					.content
					{
						width:100%;

						display:flex;
						flex-wrap:wrap;

						background-color:@brand-primary;
						margin-bottom:10px;

						.name
						{
							flex-grow:2;
						}

						.series, .repetitions, .loads, .description
						{
							input
							{
								max-width:100%;
							}
						}
						.series
						{
							width:150px;
						}
						.repetitions
						{
							width:150px;
						}
						.loads
						{
							width:150px;
						}
						.description
						{
							width:150px;
						}
					}
				}
			}

		}
	}
}

#exercise-selector-modal
{
	.modal-body
	{
		.exercises
		{
			table
			{
				tr
				{
					&.hide-row
					{
						display:none;
					}
				}
			}
		}
	}
}

.training-no-editable
{
	.objectives, .environments
	{
		font-size:18px;
	}

	.description
	{
		font-size:18px;

		margin-bottom:20px;
	}

	.video-layer
	{
		margin-bottom:20px;
	}

	.exercises
	{
		.exercise-category
		{
			.category-exercises
			{
				.exercise
				{
					.basic
					{
						.name
						{
							p
							{
								font-size:18px;
							}
						}
						.data
						{
							.current
							{
								display:flex;

								> div
								{
									padding-left:10px;
									padding-right:10px;
								}
							}
						}
					}
					.details
					{
						.images
						{
							display:flex;
							justify-content:space-around;
							align-items:center;
							margin-bottom:20px;

							img
							{
								width:40%;
								height:auto;
							}
						}
					}
				}
			}
		}
	}
	
}
